


const baseUrl = '/penalityManager/penaltyTicket'


const PenalitiesEndpoints = (builder) => 
({
    getPenalities: builder.mutation({
        query: (name) => {
          const {page, sort, entry, startDate, endDate, search, sortItem} = name
          return `searchManager/penaltyTicket?page=${page}&search=${search}&size=${entry}&sort=${sortItem} ${sort}&startDate=${startDate}&endDate=${endDate}`
        },
      }),
    deletePenality: builder.mutation({
      query: (body) => ({
        url: `${baseUrl}/${body}`,
        method: 'DELETE'
      })
    }),
    addPenality: builder.mutation({
      query: (body) => ({
        url: `${baseUrl}`,
        method: 'POST',
        body,
      })
    }),
    updatePenality: builder.mutation({
      query: (body) => ({
        url: `${baseUrl}`,
        method: 'PUT',
        body,
      })
    }),
    getPenality: builder.mutation({
      query: (body) => {
        const {page, entry, sort, startDate, endDate, search, sortItem} = body
        return `searchManager/penaltyTicket?page=${page}&search=${search}&size=${entry}&sort=${sortItem} ${sort}&startDate=${startDate}&endDate=${endDate}`
      }
    })
})

export default PenalitiesEndpoints