


const baseUrl = '/notificationManager/Notification'


const NotificationEndpoints = (builder) => 
({
    getNotifications: builder.mutation({
        query: (name) => {
          const {page, sort, entry, startDate, endDate, search, sortItem} = name
          return `searchManager/notificationLog?page=${page}&search=${search}&size=${entry}&sort=${sortItem} ${sort}&startDate=${startDate}&endDate=${endDate}`
        },
      }),
    deleteNotification: builder.mutation({
      query: (body) => ({
        url: `${baseUrl}/${body}`,
        method: 'DELETE'
      })
    }),
    addNotification: builder.mutation({
      query: (body) => ({
        url: `${baseUrl}`,
        method: 'POST',
        body,
      })
    }),
    updateNotification: builder.mutation({
      query: (body) => ({
        url: `${baseUrl}`,
        method: 'PUT',
        body,
      })
    }),
    getNotification: builder.mutation({
      query: (body) => {
        const {page, entry, sort, startDate, endDate, search, sortItem} = body
        return `searchManager/notificationLog?page=${page}&search=${search}&size=${entry}&sort=${sortItem} ${sort}&startDate=${startDate}&endDate=${endDate}`
      }
    })
})

export default NotificationEndpoints