


const baseUrl = '/parkingManager/tariff'


const TariffEndpoints = (builder) => 
({
    getTariffs: builder.mutation({
        query: (name) => {
          const {page, sort, entry, startDate, endDate, search, sortItem} = name
          return `searchManager/tariff?page=${page}&search=${search}&size=${entry}&sort=${sortItem} ${sort}&startDate=${startDate}&endDate=${endDate}`
        },
      }),
    deleteTariff: builder.mutation({
      query: (body) => ({
        url: `${baseUrl}/${body}`,
        method: 'DELETE'
      })
    }),
    addTariff: builder.mutation({
      query: (body) => ({
        url: `${baseUrl}`,
        method: 'POST',
        body,
      })
    }),
    updateTariff: builder.mutation({
      query: (body) => ({
        url: `${baseUrl}`,
        method: 'PUT',
        body,
      })
    }),
    getTariff: builder.mutation({
      query: (body) => {
        const {page, entry, sort, startDate, endDate, search, sortItem} = body
        return `searchManager/tariff?page=${page}&search=${search}&size=${entry}&sort=${sortItem} ${sort}&startDate=${startDate}&endDate=${endDate}`
      }
    }),

    getTariffById: builder.mutation({
      query: (id) => `parkingManager/tariff/${id}`
    }),

    getTariffItem: builder.mutation({
      query: (id) => `parkingManager/tariffItem/tariffId?tariffId=${id}`
    }),

    deleteTariffItem : builder.mutation({
      query: (id) => ({
        url: `parkingManager/tariffItem/${id}`,
        method: 'DELETE'
      })
    }),

    addTariffItem : builder.mutation({
      query: (body) => ({
        url: `parkingManager/tariffItem`,
        method: 'POST',
        body
      })
    }),

    updateTariffItem : builder.mutation({
      query: (body) => ({
        url: `parkingManager/tariffItem`,
        method: 'PUT',
        body
      })
    }),

})

export default TariffEndpoints