


const baseUrl = '/usersManager/users'


const userEndpoints = (builder) => 
({
    getUsers: builder.mutation({
        query: (name) => {
          const {page, sort, entry, startDate, endDate, search, sortItem} = name
          return `${baseUrl}?page=${page}&size=${entry}&searchBy=${search}&startDate=${startDate}&endDate=${endDate}`
        },
      }),
    deleteUser: builder.mutation({
      query: (body) => ({
        url: `${baseUrl}/${body}`,
        method: 'DELETE'
      })
    }),
    addUser: builder.mutation({
      query: (body) => ({
        url: `${baseUrl}`,
        method: 'POST',
        body,
      })
    }),
    updateUser: builder.mutation({
      query: (body) => ({
        url: `${baseUrl}`,
        method: 'PUT',
        body,
      })
    }),
    getUser: builder.mutation({
      query: (body) => {
        const {page, entry, sort, startDate, endDate, search, sortItem} = body
        return `searchManager/users?page=${page}&size=${entry}&searchBy=${search}&startDate=${startDate}&endDate=${endDate}`
      }
    })
})

export default userEndpoints