
const baseUrl = 'usersManager/users'


const authEndPoints = (builder) => 
({
    login: builder.mutation({
        query: (name) => `${baseUrl}`,
      }),

      reset:builder.mutation({
        query: (email) => `${baseUrl}/recover-password/${email}` 
      }),
      verifyLink: builder.mutation({
        query: (code) => `${baseUrl}/token/validate/${code}`
      }),
      changePassword: builder.mutation({
        query: (body) => {
          return(
            {
              url: `${baseUrl}//password/reset`,
              method: 'POST',
              body
            }
          )
        }
      }),
      activateAccount : builder.mutation({
        query: (otp)=> `${baseUrl}/activate/${otp}`
      }),
      changeOldPassword: builder.mutation({
        query : (body) => ({
          url: `${baseUrl}//changepassword`,
          method: 'POST',
          body
        })
      })

})

export default authEndPoints