


const baseUrl = '/ticketManager/temporalTicket'


const TemporalTicketEndpoints = (builder) => 
({
    getTemporalTickets: builder.mutation({
        query: (name) => {
          const {page, sort, entry, startDate, endDate, search, sortItem} = name
          return `searchManager/temporalTicket?page=${page}&search=${search}&size=${entry}&sort=${sortItem} ${sort}&startDate=${startDate}&endDate=${endDate}`
        },
      }),
    deleteTemporalTicket: builder.mutation({
      query: (body) => ({
        url: `${baseUrl}/${body}`,
        method: 'DELETE'
      })
    }),
    addTemporalTicket: builder.mutation({
      query: (body) => ({
        url: `${baseUrl}`,
        method: 'POST',
        body,
      })
    }),
    updateTemporalTicket: builder.mutation({
      query: (body) => ({
        url: `${baseUrl}`,
        method: 'PUT',
        body,
      })
    }),
    getTemporalTicket: builder.mutation({
      query: (body) => {
        const {page, entry, sort, startDate, endDate, search, sortItem} = body
        return `searchManager/temporalTicket?page=${page}&search=${search}&size=${entry}&sort=${sortItem} ${sort}&startDate=${startDate}&endDate=${endDate}`
      }
    })
})

export default TemporalTicketEndpoints