


const baseUrl = '/profileManager/profile'


const ProfileEndpoints = (builder) => 
({
    getProfiles: builder.mutation({
        query: (name) => {
          const {page, sort, entry, startDate, endDate, search, sortItem} = name
          return `searchManager/profile?page=${page}&search=${search}&size=${entry}&sort=${sortItem} ${sort}&startDate=${startDate}&endDate=${endDate}`
        },
      }),
    deleteProfile: builder.mutation({
      query: (body) => ({
        url: `${baseUrl}/${body}`,
        method: 'DELETE'
      })
    }),
    addProfile: builder.mutation({
      query: (body) => ({
        url: `${baseUrl}`,
        method: 'POST',
        body,
      })
    }),
    updateProfile: builder.mutation({
      query: (body) => ({
        url: `${baseUrl}`,
        method: 'PUT',
        body,
      })
    }),
    getProfile: builder.mutation({
      query: (body) => {
        const {page, entry, sort, startDate, endDate, search, sortItem} = body
        return `searchManager/profile?page=${page}&search=${search}&size=${entry}&sort=${sortItem} ${sort}&startDate=${startDate}&endDate=${endDate}`
      }
    }),
    exportExcel: builder.mutation(({
      query: (name) => 'searchManager/profile?page=0&search=*&size=500&sort=id%20desc&startDate=&endDate='
    })),
    getProfileById: builder.mutation({
        query: id => `${baseUrl}/${id}`
    }),

    getVehicleAccess: builder.mutation({
      query: profileId => `vehicleManager/VehicleAccess/customerProfile/${profileId}`
    })

})

export default ProfileEndpoints