


const baseUrl = '/vehicleManager/vehicle'


const vehicleEndPoints = (builder) => 
({
    getVehicles: builder.mutation({
        query: (name) => {
          const {page, sort, entry, startDate, endDate, search, sortItem} = name
          return `searchManager/vehicle?page=${page}&search=${search}&size=${entry}&sort=${sortItem} ${sort}&startDate=${startDate}&endDate=${endDate}`
        },
      }),
    deleteVehicle: builder.mutation({
      query: (body) => ({
        url: `${baseUrl}/${body}`,
        method: 'DELETE'
      })
    }),
    addVehicle: builder.mutation({
      query: (body) => ({
        url: `${baseUrl}`,
        method: 'POST',
        body,
      })
    }),
    updateVehicle: builder.mutation({
      query: (body) => ({
        url: `${baseUrl}`,
        method: 'PUT',
        body,
      })
    }),
    getVehicle: builder.mutation({
      query: (body) => {
        const {page, entry, sort, startDate, endDate, search, sortItem} = body
        return `searchManager/vehicle?page=${page}&search=${search}&size=${entry}&sort=${sortItem} ${sort}&startDate=${startDate}&endDate=${endDate}`
      }
    }), 
    getVehicleModelById: builder.mutation({
      query: (id) => {
        return `vehicleManager/vehicleModel/vehicleBrand/${id}`
      }
    }),

    uploadImage: builder.mutation({
      query: (body) => ({
        url: `fileManager`,
        method: 'POST',
        body,
      })
    }),
    uploadSideImage:builder.mutation({
      query: (body) => ({
        url: `fileManager`,
        method: 'POST',
        body,
      })
    }),
    getVehicleById : builder.mutation({
      query: id => `${baseUrl}/${id}`
    }),

    getSideImage: builder.query({
      query: id => `fileManager/${id}`
    }),
    getFrontImage: builder.query({
      query: id => `fileManager/${id}`
    }),

    downloadDocument: builder.mutation({
      query: id => `fileManager/${id}/download`
    }),

    getVehicleAccessProfiles: builder.mutation({
      query: numberPlate => `vehicleManager/VehicleAccess/plateNumber/${numberPlate}`
    })
})

export default vehicleEndPoints