


const baseUrl = 'vehicleManager/vehicleModel'


const vehicleModelEndpoints = (builder) => 
({
    getVehicleModels: builder.mutation({
      query: (name) => {
        const {page, sort, entry, startDate, endDate, search, sortItem} = name
        return `searchManager/vehicleModel?page=${page}&search=${search}&size=${entry}&sort=${sortItem} ${sort}&startDate=${startDate}&endDate=${endDate}`
      },
      }),
    deleteVehicleModel: builder.mutation({
      query: (body) => ({
        url: `${baseUrl}/${body}`,
        method: 'DELETE'
      })
    }),
    addVehicleModel: builder.mutation({
      query: (body) => ({
        url: `${baseUrl}`,
        method: 'POST',
        body,
      })
    }),
    updateVehicleModel: builder.mutation({
      query: (body) => ({
        url: `${baseUrl}`,
        method: 'PUT',
        body,
      })
    }),
    getVehicleModel: builder.mutation({
      query: (body) => {
        const {page, entry, sort, startDate, endDate, search, sortItem} = body
        return `searchManager/vehicleModel?page=${page}&search=${search}&size=${entry}&sort=${sortItem} ${sort}&startDate=${startDate}&endDate=${endDate}`
      }
    })
})

export default vehicleModelEndpoints