


const baseUrl = '/parkingManager/parkingArea'


const ParkingAreaEndpoints = (builder) => 
({
    getParkingAreas: builder.mutation({
        query: (name) => {
          const {page, sort, entry, startDate, endDate, search, sortItem} = name
          return `searchManager/parkingArea?page=${page}&search=${search}&size=${entry}&sort=${sortItem} ${sort}&startDate=${startDate}&endDate=${endDate}`
        },
      }),
    deleteParkingArea: builder.mutation({
      query: (body) => ({
        url: `${baseUrl}/${body}`,
        method: 'DELETE'
      })
    }),
    addParkingArea: builder.mutation({
      query: (body) => ({
        url: `${baseUrl}`,
        method: 'POST',
        body,
      })
    }),
    updateParkingArea: builder.mutation({
      query: (body) => ({
        url: `${baseUrl}`,
        method: 'PUT',
        body,
      })
    }),
    getParkingArea: builder.mutation({
      query: (body) => {
        const {page, entry, sort, startDate, endDate, search, sortItem} = body
        return `searchManager/parkingArea?page=${page}&search=${search}&size=${entry}&sort=${sortItem} ${sort}&startDate=${startDate}&endDate=${endDate}`
      }
    }),
    getDurationUnit: builder.mutation({
      query: () => `parkingManager/DurationUnit`
    }),
    getParkingSpotByAreaId: builder.query({
      query: (id) => `parkingManager/parkingSpot/parkingArea/${id}`
    }),
    addParkingSportByArea: builder.mutation({
      query : (body) => ({
        url: '/parkingManager/parkingSpot',
        method: 'POST',
        body
      })
    }),
    deleteParkingSportByArea: builder.mutation({
      query : (id) => ({
        url: `/parkingManager/parkingSpot/${id}`,
        method: 'DELETE'
      })
    }),

    getOccupationStatus: builder.query({
      query : (id) => `parkingManager/ParkingSpotOccupationStatus`,
    }),
    getLocations: builder.query({
      query : (id) => `locationsManager/locations/complete/${id}`
    }),
    getParkingTariff: builder.query({
      query : (id) => `parkingManager/parkingTariff/parkingArea/${id}`
    }),
    addTariffByParkingArea: builder.mutation({
      query : (body) => {
        return{
          url: 'parkingManager/parkingTariff',
          method: 'POST',
          body
        }
      }
    }),
    getParkingTariffTicket: builder.query({
      query : (id) => `parkingManager/parkingTariff/parkingArea/${id}/tariffType/1`

    }),
    getParkingTariffPenality: builder.query({
      query : (id) => `parkingManager/parkingTariff/parkingArea/${id}/tariffType/2`
    }),
    getAgentsByParkingArea: builder.mutation({
      query: (id) => `searchManager/agentDeployment?page=0&search=${id}&size=24&sort=id%20desc`
    }),
    viewParkingAgentDetails: builder.mutation({
      query: (id) => `fieldAgentManager/AgentDeployment/${id}`
    }),
    deleteAgentByParkingArea: builder.mutation({
      query: (id) => `fieldAgentManager/AgentDeployment/${id}`
    })
})

export default ParkingAreaEndpoints