


const baseUrl = '/vehicleManager/vehicleOwnership'


const vehicleOwnershipEndPoints = (builder) => 
({
    getVehicleOwnerships: builder.mutation({
        query: (name) => {
          const {page, sort, entry, startDate, endDate, search, sortItem} = name
          return `searchManager/vehicleOwnership?page=${page}&search=${search}&size=${entry}&sort=${sortItem} ${sort}&startDate=${startDate}&endDate=${endDate}`
        }
    }),
    deleteVehicleOwnership: builder.mutation({
      query: (body) => ({
        url: `${baseUrl}/${body}`,
        method: 'DELETE'
      })
    }),
    addVehicleOwnership: builder.mutation({
      query: (body) => ({
        url: `${baseUrl}`,
        method: 'POST',
        body,
      })
    }),
    updateVehicleOwnership: builder.mutation({
      query: (body) => ({
        url: `${baseUrl}`,
        method: 'PUT',
        body,
      })
    }),
    getVehicleOwnership: builder.mutation({
      query: (body) => {
        const {page, entry, sort, startDate, endDate, search, sortItem} = body
        return `searchManager/vehicleOwnership?page=${page}&search=${search}&size=${entry}&sort=${sortItem} ${sort} &startDate=${startDate}&endDate=${endDate}`
      }
    }),
    fetchVehicleOwnerProfile: builder.mutation({
      query: id => `${baseUrl}/vehicle/${id}`
    })
})

export default vehicleOwnershipEndPoints