import vehicleEndPoints from './endpoints/vehicle/vehicle'
import TariffEndpoints from './endpoints/tariff/tariff'
import TicketEndpoints from './endpoints/tickets/Tickets'
import PayementEndpoints from './endpoints/payments/Payments'
import ParkingAreaEndpoints from './endpoints/parking/ParkingArea'
import ParkingSpotEndpoints from './endpoints/parking/ParkingSpot'
