import PaymentTypeEndpoints from './endpoints/payments/PaymentType'
import TemporalTicketEndpoints from './endpoints/tickets/TemporalTicket'
import PenalitiesEndpoints from './endpoints/penalities/Penalities'
import NotificationEndpoints from './endpoints/notification/Notification'




const baseQuery = fetchBaseQuery({
  baseUrl: process.env.REACT_APP_D_CORE_URL,
  prepareHeaders: (headers) => {
    const token = localStorage.getItem('token')
    headers.set('Content-Type', 'application/json')
    headers.set('accept', 'application/json')
    headers.set('Authorization', `Bearer ${token}`)

    return headers
  },
})

const baseQueryIntercep = async (args, api, extraOptions) => {
  let queryResult = await baseQuery(args, api, extraOptions)
  if(queryResult.error && queryResult.error?.status === 401 && api.endpoint !== 'getMe') {
    /* 
      TODO: Logout the user through the logout reducer, this should trigger a rerender
      at the App component level, and render the /login page.
    */
  }

  return queryResult
}

const appApi = createApi({
  reducerPath: 'data',
  baseQuery: baseQueryIntercep,
  tagTypes: [
    'auth'
  ],
  endpoints: (builder) => ({
    ...vehicleTypeEndPoints(builder),
    ...vehicleOwnershipEndPoints(builder),
    ...vehicleBrandEndPoints(builder),
    ...vehicleModelEndpoints(builder),
    ...AddressTypeEndPoints(builder),
    ...authEndPoints(builder),
    ...profileTypeEndpoints(builder),
    ...documentTypeEndpoints(builder),
    ...ProfileEndpoints(builder),
    ...vehicleEndPoints(builder),
    ...TariffEndpoints(builder),
    ...TicketEndpoints(builder),
    ...PayementEndpoints(builder),
    ...ParkingAreaEndpoints(builder),
    ...ParkingSpotEndpoints(builder),
    ...userEndpoints(builder),
    ...NotificationLogsEndpoints(builder),
    ...TariffTypeEndpoints(builder),
    ...PayableTypeEndpoints(builder),
    ...PaymentTypeEndpoints(builder),
    ...TemporalTicketEndpoints(builder),
    ...PenalitiesEndpoints(builder),
    ...NotificationEndpoints(builder)
  }),
})

export default appApi

