


const baseUrl = '/paymentManager/paymentType'


const PaymentTypeEndpoints = (builder) => 
({
    getPayamentTypes: builder.mutation({
        query: (name) => {
          const {page, sort, entry, startDate, endDate, search, sortItem} = name
          return `searchManager/paymentType?page=${page}&search=${search}&size=${entry}&sort=${sortItem} ${sort}&startDate=${startDate}&endDate=${endDate}`
        },
      }),
    deletePayamentType: builder.mutation({
      query: (body) => ({
        url: `${baseUrl}/${body}`,
        method: 'DELETE'
      })
    }),
    addPayamentType: builder.mutation({
      query: (body) => ({
        url: `${baseUrl}`,
        method: 'POST',
        body,
      })
    }),
    updatePayamentType: builder.mutation({
      query: (body) => ({
        url: `${baseUrl}`,
        method: 'PUT',
        body,
      })
    }),
    getPayamentType: builder.mutation({
      query: (body) => {
        const {page, entry, sort, startDate, endDate, search, sortItem} = body
        return `searchManager/paymentType?page=${page}&search=${search}&size=${entry}&sort=${sortItem} ${sort}&startDate=${startDate}&endDate=${endDate}`
      }
    })
})

export default PaymentTypeEndpoints