


const baseUrl = '/parkingSpotManager/parkingSpot'


const ParkingSpotEndpoints = (builder) => 
({
    getParkingSpots: builder.mutation({
        query: (name) => {
          const {page, sort, entry, startDate, endDate, search, sortItem} = name
          return `searchManager/parkingSpot?page=${page}&search=${search}&size=${entry}&sort=${sortItem} ${sort}&startDate=${startDate}&endDate=${endDate}`
        },
      }),
    deleteParkingSpot: builder.mutation({
      query: (body) => ({
        url: `${baseUrl}/${body}`,
        method: 'DELETE'
      })
    }),
    addParkingSpot: builder.mutation({
      query: (body) => ({
        url: `${baseUrl}`,
        method: 'POST',
        body,
      })
    }),
    updateParkingSpot: builder.mutation({
      query: (body) => ({
        url: `${baseUrl}`,
        method: 'PUT',
        body,
      })
    }),
    getParkingSpot: builder.mutation({
      query: (body) => {
        const {page, entry, sort, startDate, endDate, search, sortItem} = body
        return `searchManager/parkingSpot?page=${page}&search=${search}&size=${entry}&sort=${sortItem} ${sort}&startDate=${startDate}&endDate=${endDate}`
      }
    })
})

export default ParkingSpotEndpoints